import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { ApiServices } from './api.service';
import { PopupService } from './popup.service';

@Injectable()
export class AuthService {

  userSubject = new Subject<any>();
  csrf;
  csrfSubject = new Subject<any>();
  user;

  constructor(
    public apiServices: ApiServices,
    public popupService: PopupService) {
  }

  public initApi() {
    this.apiServices.initApi().subscribe(res => {
      this.setCsrf();
      if (res) {
        this.setUser(res);
      }
    });
  }

  public setCsrf() {
    const cookies = document.cookie.split(';');
    cookies.forEach(cookie => {
      const key = cookie.split('=')[0].trim();
      if (key === 'XSRF-TOKEN') {
        this.csrf = cookie.split('=')[1];
        sessionStorage.setItem('csrf', this.csrf);
        this.csrfSubject.next(this.csrf);
      }
    });
  }

  public getCsrf() {
    return this.csrfSubject.asObservable();
  }

  public hasUser() {
    if (!this.user) {
      this.popupService.toggleLoginModal();
      return false;
    }
    return true;
  }

  public isAuthenticated(): boolean {
    if (!this.user) {
      this.popupService.toggleLoginModal();
      return false;
    }
    return true;
  }

  public isVerified(): boolean {
    if (this.isAuthenticated()) {
      if (!this.user.verified) {
        this.popupService.toggleVerificationModal();
        return false;
      }
      return true;
    }
    return false;
  }

  public fetchUser() {
    if (this.isAuthenticated()) {
      this.apiServices.getUser().subscribe(user => {
        this.setUser(user);
      }, err => {
        if (err.error.message === 'jwt expired') {
          this.setUser(null);
        }
      });
    }
  }

  public getUser() {
    return this.userSubject.asObservable();
  }

  public setUser(user) {
    this.user = user;
    this.userSubject.next(user);
  }

  public logout() {
    this.user = null;
    this.userSubject.next(null);
    this.apiServices.logout().subscribe(res => {
      console.log(res);
    });
  }
}
