import { Component, OnInit } from '@angular/core';
import { fromEvent } from 'rxjs';

import { PostService } from '../service/post.service';
import { Post } from '../model/post.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html'
})
export class HomeComponent implements OnInit {

  sortings = ['Fresh', 'Popular'];
  sortBy = 'Fresh';
  newPosts = true;
  totalPosts;
  posts: Post[];
  fetching = false;
  page = 1;
  user;

  constructor(
    private postService: PostService,
  ) {
    this.fetching = true;
    this.postService.fetchPosts(this.page);
  }

  ngOnInit() {
    this.postService.getPosts().subscribe(posts => {
      const count = posts[0].postsCount;
      if (!this.totalPosts) {
        this.totalPosts = count;
      }
      if (this.totalPosts && this.totalPosts < count) {
      }
      this.posts = [];
      this.fetching = false;
      this.posts = posts;
    });

    fromEvent(document, 'scroll').subscribe(event => {
      if (this.totalPosts === this.posts.length) {
        return;
      }
      if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
        if (!this.fetching) {
          this.fetching = true;
          this.page += 1;
          if (this.sortBy === 'Popular') {
            return this.postService.fetchPopularPosts(this.page);
          }
          return this.postService.fetchPosts(this.page);
        }
      }
    });
  }

  hasLikedPost(postvote) {
    return this.postService.hasLikedPost(postvote);
  }

  hasSavedPost(postheart) {
    return this.postService.hasSavedPost(postheart);
  }

  toggleLike(postId) {
    this.postService.likePost(postId);
  }

  toggleSave(postId) {
    this.postService.savePost(postId);
  }

  toggleDetail(post) {
    window.location.hash = 'postid=' + post.id;
  }

  isVideo(url) {
    const ext = url.split('.')[1];
    if (ext === 'mp4' || ext === 'vid') {
      return 'video';
    }
  }

  sortPosts(sort) {
    if (this.sortBy === sort) {
      return;
    } else {
      this.sortBy = sort;
      this.posts = [];
      this.page = 1;
      // window.scroll(0, 0);
      this.fetching = true;
      if (this.sortBy === 'Popular') {
        return this.postService.fetchPopularPosts(this.page);
      } else {
        return this.postService.fetchPosts(this.page);
      }
    }
  }

  getTags(tags) {
    if (tags === '' || tags === undefined || tags === null) {
      return [];
    }
    return tags.split(',');
  }

  timeDiff(time) {
    return this.postService.getTimeDiff(time);
  }

}
